<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import Oss from '@/components/common/oss'
import Clip from '@/components/common/clip'
import AccelerateAduit from '@/components/dialog/accelerateAduit'
import qs from 'qs'
import { UPLOAD_TYPE } from '@/config/constant'

import { isIOS, transferBytes, watermark } from '@/utils/tools'
import Storage from '@/utils/Storage'
import wxApi from '@/utils/wxAuth'

//  activated 
let isFirstEnter = true

export default {
  name: 'BaseInfo',
  components: {
    Clip,
    AccelerateAduit,
    Oss
  },
  computed: {
    ...mapState({
      userData: 'userData',
      sexMap: 'sexMap',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      channelMap: 'channelMap',
      ifStudentMap: 'ifStudentMap',
      areaData: 'areaData'
    }),
    filterAreaData () {
      let a = JSON.parse(JSON.stringify(this.areaData))
      delete a.province_list['100000']
      delete a.city_list['100100']
      return a
    },
    ...mapGetters({
      userId: 'userId',
      mId: 'mId'
    }),
    isEditable () {
      return this.userData.basedataAuth !== 1
    },
    statusName () {
      if (this.userData.firstBaseAuth) {
        if (this.userData.basedataAuth === 1) {
          return ''
        }
        if (this.userData.basedataAuth === 2) {
          return ''
        }
        if (this.userData.basedataAuth === 3) {
          // return ''
          return ''
        }
      } else if (this.userData.basedataAuth === 2) {
        return ''
      }
    },
    salaryArr () {
      let arr = []
      for (let i in this.salaryMap) {
        arr.push({
          value: i,
          text: this.salaryMap[i]
        })
      }
      // 
      arr.shift(0)
      return arr
    },
    statusArr () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      // 
      arr.shift(0)
      arr.pop()
      arr.pop()
      return arr
    },
    assetsArr () {
      let arr = []
      for (let i in this.assetsMap) {
        arr.push({
          value: i,
          text: this.assetsMap[i]
        })
      }
      return arr
    },
    channelArr () {
      let arr = []
      for (let i in this.channelMap) {
        arr.push({
          value: i,
          text: this.channelMap[i]
        })
      }
      return arr
    },
    ifStudentArr () {
      let arr = []
      for (let i in this.ifStudentMap) {
        arr.push({
          value: i,
          text: this.ifStudentMap[i]
        })
      }
      return arr
    }
  },
  data () {
    return {
      isEditing: false,
      clipParams: {original: 2, type: UPLOAD_TYPE.cover},
      coverParams: { type: UPLOAD_TYPE.cover },
      wxCodeParams: { type: UPLOAD_TYPE.wxCode },
      coverFile: null, // file
      wxCodeFile: null,
      isUpdateInfo: false,
      areaColumns: 2,
      isNativeForeign: false, // 
      isForeign: false, // 
      showPicker: false,
      pickerType: 1,
      pickerData: [],
      showChatCodePop: false,
      infoData: {
      },
      limitLength: 500,
      isShowDialog: false,
      isShowClip: false,
      // 
      coverClipFile: '',
      // 
      tempImg: '',
      // oss
      clipOriginFile: '',
      isShowPreview: false,
      previewIndex: 0,
      timer: 0,
      // 
      showUploadRequestModal: false,
      // trigger
      showUploadRequest: false
    }
  },
  watch: {
    '$route' (to, from) {
      if (this.$route.query.upd) {
        this.isUpdateInfo = true
      } else {
        this.isUpdateInfo = false
      }
    }
  },
  methods: {
    ...mapActions({
      fetchUserData: 'fetchUserData'
    }),
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    // 
    calculater (year) {
      var arr1 = ['', '', '', '', '', '', '', '', '', '', '', '']
      return /^\d{4}$/.test(year) ? `${arr1[year % 12]}` : false
    },
    // 
    selectedImg (e) {
      if (!e.file) {
        return this.$toast('')
      }
      this.tempImg = e.file
      this.isShowClip = true
    },
    selectedOriginImg (e) {
      this.coverFile = e.file
    },

    coverPicSuccess ({ picId, url, originId, originUrl }) {
      this.infoData.coverPicUrlList[0] = url
      this.infoData.coverPicList[0] = picId
      this.infoData.topPicId = originId
      this.coverClipFile = null
      this.tempImg = null
    },

    originPicSuccess ({ picId, url }) {
      this.infoData.coverPicUrlList.push(url)
      this.infoData.coverPicList.push(picId)
      this.coverFile = null
    },

    WebchatCodeSuccess ({ picId, url }) {
      this.infoData.wechatPicUrl = url
      this.infoData.wechatPicId = picId
      if (this.showChatCodePop) this.showChatCodePop = false
    },

    clipComplete ({ data }) {
      this.coverClipFile = data
      this.clipOriginFile = this.tempImg
      this.isShowClip = false
    },
    deleteClipCover () {
      this.infoData.clipCoverPicUrl = ''
    },
    deleteImg (index) {
      if (index === 0) {
        this.$set(this.infoData.coverPicList, 0, '')
        this.$set(this.infoData.coverPicUrlList, 0, '')
        this.infoData.topPicId = ''
      } else {
        this.infoData.coverPicList.splice(index, 1)
        this.infoData.coverPicUrlList.splice(index, 1)
      }
    },

    switchwebchatPop () {
      if (this.isEditable) {
        this.showChatCodePop = true
      }
    },
    // 
    selectedWxCode (e) {
      this.wxCodeFile = e.file
    },
    switchEducation () {
      if (!this.isEditable) return
      this.infoData.schollVisible = this.infoData.schollVisible === 1 ? 0 : 1
    },
    switchComVisible () {
      if (!this.isEditable) return
      this.infoData.companyVisible = this.infoData.companyVisible === 1 ? 0 : 1
    },
    switchSalaryVisible () {
      if (!this.isEditable) return
      this.infoData.annualSalaryVisible = this.infoData.annualSalaryVisible === 1 ? 0 : 1
    },
    /**
     * @description picker
     * @param type { Number }
     *    0: 
     *    1: 
     *    2: 
     *    3: 
     *    4: 
     */
    triggerPicker (type) {
      if (!this.isEditable) return
      this.pickerType = type

      switch (type) {
        case 0:
          if (this.infoData.nativeCityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 1:
          if (this.infoData.cityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 2:
          this.pickerData = this.salaryArr
          break
        case 3:
          this.pickerData = this.statusArr
          break
        case 4:
          this.pickerData = this.assetsArr
          break
        case 5:
          this.pickerData = this.channelArr
          break
        case 6:
          this.pickerData = this.ifStudentArr
          break
      }

      this.showPicker = true
    },
    handleDialogcancel () {
      this.showPicker = false
    },
    handleAreachange (picker, res) {
      if (res[0].code === '900000') {
        this.areaColumns = 1
      } else {
        this.areaColumns = 2
      }
    },
    handleAreaconfirm (res) {
      if (!res[0].code) {
        this.showPicker = false
        return
      }
      this.showPicker = false

      // this.infoData.provice = res[0]
      // 
      if (res[0].code === '900000') {
        if (this.pickerType === 0) {
          this.isNativeForeign = true
          this.infoData.nativeCityId = res[0].code
          this.infoData.nativeLocation = ''
          // this.infoData.nativeLocation = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        } else {
          this.isForeign = true
          this.infoData.cityId = res[0].code
          this.infoData.locationCity = ''
          // this.infoData.locationCity = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        }
      } else {
        if (this.pickerType === 0) {
          this.isNativeForeign = false
          this.infoData.nativeCityId = res[1].code
          this.infoData.nativeLocation = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        } else {
          this.isForeign = false
          this.infoData.locationCity = `${res[1].name}`
          this.infoData.cityId = res[1].code
        }
      }
    },
    handlePickerConfirm (res) {
      this.showPicker = false
      let val = parseInt(res.value)
      switch (this.pickerType) {
        case 2:
          this.infoData.annualSalary = val
          break
        case 3:
          this.infoData.singleStatus = val
          break
        case 4:
          this.infoData.richesInfo = val
          break
        case 5:
          this.infoData.channelCode = val
          break
        case 6:
          this.infoData.ifStudent = val
          break
      }
    },
    switchPost (val) {
      if (!this.isEditable) return
      this.infoData.ifPost = val
    },
    /**
     * @description 
     * type 1-; undefined-, 1
     */
    saveDraft (type) {
      let typeIn = type
      if (!this.isEditable) {
        return false
      }
      try {
        clearTimeout(this.timer)
        // 
        let tempData = Object.assign({}, this.infoData)
        delete tempData.coverPicUrlList

        if (tempData.coverPicList.join('') === '') {
          delete tempData.coverPicList
        }
        Storage.saveOnday('draftData', tempData, 2)
        if (typeIn === 1) {
          this.$toast('')
        }
        this.timer = setTimeout(this.saveDraft, 5000)
      } catch (err) {
        this.$toast(err)
      }
    },
    /**
     * @description 
     */
    confirm () {
      this.isShowDialog = false
      this.$router.back()
    },
    /**
     * @description 
     */
    handleSubmit () {
      if (!this.isEditable) return
      // 
      let fd = this.infoData
      if (!fd.coverPicList || !fd.coverPicList[0]) {
        return this.$toast('')
      }
      if (!fd.coverPicList || fd.coverPicList.length < 2) {
        return this.$toast('')
      }
      if (!fd.name) {
        return this.$toast('')
      }
      if (!fd.nickName) {
        return this.$toast('')
      }
      if (this.isForeign) {
        if (!fd.country || !fd.state) {
          return this.$toast('')
        }
        fd.locationCity = fd.country + fd.state
      } else {
        // TODO: 
        if (!fd.cityId) {
          return this.$toast('')
        }
      }
      if (this.isNativeForeign) {
        if (!fd.nativeCountry || !fd.nativeState) {
          return this.$toast('')
        }
        fd.nativeLocation = fd.nativeCountry + fd.nativeState
      } else {
        // TODO: 
        if (!fd.nativeCityId) {
          return this.$toast('')
        }
      }
      if (!fd.school) {
        return this.$toast('')
      }
      if ((typeof fd.ifStudent).toLowerCase() !== 'number') {
        return this.$toast('')
      }
      if (!fd.ifStudent) {
        if (!fd.job) {
          return this.$toast('')
        }
        if (!fd.company) {
          return this.$toast('')
        }
        if (!fd.industry) {
          return this.$toast('')
        }
        if (!fd.annualSalary) {
          return this.$toast('')
        }
      }
      if (!fd.singleStatus) {
        return this.$toast('')
      }
      if (!fd.richesInfo) {
        return this.$toast('')
      }
      if (!fd.channelCode) {
        return this.$toast('hello')
      } else if (fd.channelCode === 99 && !fd.otherChannel) {
        return this.$toast('')
      }
      if (!fd.selfDescription) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.hobby) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.familyInfo) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.personalExperience) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.ideaPartner) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.loveMind) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.wechatPicId) {
        return this.$toast('')
      }
      if (fd.ifPost === -1) {
        return this.$toast('hello')
      }

      // 
      // pending
      /* delete fd.identityPicList
      delete fd.identityPicUrlList
      delete fd.identityAuthReason
      delete fd.identityAuthTime
      delete fd.identityCommitTime
      delete fd.workPicList
      delete fd.workPicUrlList
      delete fd.workAuthReason
      delete fd.workAuthTime
      delete fd.workCommitTime
      delete fd.educationPicList
      delete fd.educationPicUrlList
      delete fd.educationAuthReason
      delete fd.educationAuthTime
      delete fd.educationCommitTime */

      // 
      this.infoData.basedataAuth = 1

      this.$root.openLoading()
      this.$http('/api/member/saveMemberPending', this.infoData).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000' || res.code === '200003') {
          // TODO: 
          if (res.code === '0000') {
            this.updateUserData({
              basedataAuth: 1
            })
          } else {
            this.updateUserData({ singleStatus: this.userData.singleStatus })
          }
          this.isShowDialog = true
          // 
          clearTimeout(this.timer)
          Storage.remove('draftData')
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    showPreview (index) {
      this.previewIndex = index
      this.isShowPreview = true
    },
    changePreview (index) {
      this.$refs['swipe'].swipeTo(index)
    },
    closeRequestModal () {
      this.showUploadRequestModal = false
      this.showUploadRequest = false
      this.$nextTick(() => {
        let dom = document.querySelector('#uploadClipBtn input')
        dom && dom.click()
        let _dom = document.querySelector('#upload-request-mask')
        _dom && _dom.remove()
      })
    }
  },
  mounted () {
    this.$http('/api/member/getMemberPending', {}, 'get').then(res => {
      this.$root.closeLoading()
      if (res.code === '0000') {
        let draftData = {}
        if (this.isEditable) {
          draftData = Storage.loadOntime('draftData')
        }
        draftData = { ...res.data, ...draftData }
        // 
        if (!draftData.coverPicList) {
          draftData.coverPicList = ['']
          draftData.coverPicUrlList = ['']
        }

        let arr = [], idArr = []
        if (draftData.coverPicList && draftData.coverPicList.join() !== '')  {
          idArr = [].concat(draftData.coverPicList)
        }
        if (draftData.wechatPicId) {
          idArr.push(draftData.wechatPicId)
        }
        if (idArr.length) {
          this.$http(`/api/member/image/urls`, {
            picIds: idArr.join(',')
          }, 'get').then(res => {
            if (res.code === '0000') {
              draftData.coverPicList.map(_ => {
                arr.push(res.data[_])
              })
              // url
              draftData.wechatPicUrl = res.data[draftData.wechatPicId]
            }
            draftData.coverPicUrlList = arr
          })
        }

        // idid
        if (!draftData.topPicId) {
          draftData.topPicId = draftData.coverPicList && draftData.coverPicList[0]
        }

        // 
        draftData.cityId = '' + (draftData.cityId ? draftData.cityId : '')
        draftData.nativeCityId = '' + (draftData.nativeCityId ? draftData.nativeCityId : '')

        // 
        if (draftData.cityId === '900000') {
          this.isForeign = true
        } else {
          this.isForeign = false
        }
        if (draftData.nativeCityId === '900000') {
          this.isNativeForeign = true
        } else {
          this.isNativeForeign = false
        }

        this.infoData = draftData

        this.saveDraft()
      } else {
        this.$toast(res.msg)
      }
    }).catch(err => {
      this.$toast('')
    })
  },
  activated () {
    if (this.infoData.id) {
      this.saveDraft()
    }
    // 
    //24
    let intervalTime = 8640000
    let dateTime =Date.parse(new Date())
    let dateTimeOld = Storage.load('showUploadRequestDateTime')
    if (dateTimeOld) {
      if ((dateTime-dateTimeOld) >= intervalTime){
        this.showUploadRequest = true
        // 
        Storage.save('showUploadRequestDateTime', dateTime)
      }else {
        this.showUploadRequest = false
      }

    } else {
      this.showUploadRequest = true
      Storage.save('showUploadRequestDateTime', dateTime)
    }
    let d = this.userData
    if (this.userId && d.firstBaseAuth) {
      wxApi.configShare({
        title: 'Hello',
        // desc: 'ID----',
        desc: `${this.mId}-${this.educationMap[d.education]}-${d.ifStudent ? '' : d.job}-${d.age}-${parseInt(d.cityId) === 900000 ? (d.country + ' ' + d.state) : this.areaData['city_list'][d.cityId]}`,
        link: `${location.origin}/info-${this.mId}.html`,
        imgUrl: `${d.headPicUrl}`
      })
    } else {
      wxApi.configShare()
    }
  },
  deactivated () {
    clearTimeout(this.timer)
    wxApi.configShare()
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 
      vm.showChatCodePop = false
      vm.showUploadRequestModal = false
      // 
      if (!vm.userData.ifMobileVerify) {
        vm.$router.replace({
          path: '/setUserData/phone.html'
        })
      } else if (to.query.clearCache) {
        Storage.remove('draftData')
        next({
          path: '/baseInfo.html'
        })
      }
    })
  }
}
</script>

